.project-page-container {
  display: flex;
  max-width: 85vw;
  margin: 2rem auto;
}

/* Sidebar TOC */
.line-decoration {
  margin: 0.5rem 0 0.2rem 0;
  border: none;
  border-top: 2px solid var(--pink);
  width: 75%;
}

.toc {
  flex: 0 0 220px;
  position: sticky;
  top: 9%;
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  font-family: var(--lora);
}

.toc-title {
  margin: 0;
  color: var(--pink);
}

.toc ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  font-family: var(--chivo);
}

.toc ul li {
  margin: 0.5rem 0;
}

.toc-link {
  color: var(--black);
  text-decoration: none;
  transition: color 0.2s, transform 0.2s;
  font-size: var(--fs-md);
  display: inline-block;
}

.toc-link:hover {
  color: var(--pink);
  transform: translateX(5px);
}

.back-button {
  background: none;
  border: none;
  font-size: clamp(var(--fs-xs), 1.5rem, var(--fs-2xl));
  font-family: var(--chivo);
  transition: color 0.3s, transform 0.2s;
  color: var(--pink);
  display: flex;
  flex-direction: flex-start;
  margin-top: 0.2rem;
}

.back-button:hover {
  color: var(--black);
  transform: translateX(5px);
}

.back-button.toc {
  font-size: clamp(var(--fs-sm), 1rem, var(--fs-xl)) !important;
}

.back-button.mobile {
  display: none;
}

.project-page {
  display: block;
  margin-right: 2rem;
  flex: 1;
  padding-bottom: 2rem;
}

.text-header {
  font-family: var(--chivo);
  margin-bottom: 0.5rem;
  color: var(--black);
  font-size: clamp(var(--fs-md), 1.7vw, var(--fs-lg));
}

.text {
  font-family: var(--chivo);
  margin-top: 0;
  line-height: 1.5;
  color: var(--black);
  font-size: clamp(var(--fs-sm), 1.5vw, var(--fs-md));
  text-indent: 1rem;
}

.list {
  font-family: var(--chivo);
  margin-top: 0;
  line-height: 1.5;
  color: var(--black);
  font-size: clamp(var(--fs-sm), 1.5vw, var(--fs-md));
}

.highlight {
  color: var(--pink);
  text-decoration: underline;
  text-underline-offset: 3.5px;
}

.project-page-header {
  display: flex;
  flex-direction: row;
  margin-top: 15px;
  margin-bottom: 35px;
}

.media-grid {
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(250px, 1fr)
  ); /* smaller min width so 3 fit */
  gap: 1rem;
  margin-top: 1rem;
}

.media-item {
  object-fit: cover;
}

/* Phone screenshots = smaller, 3 across */
.phone-media {
  grid-column: span 1;
  max-height: 600px;
  width: auto;
  object-fit: cover;
  border-radius: 2.2rem;
}

/* Web screenshots = bigger, take more space */
.web-media {
  grid-column: span 2; /* stays wider */
  max-height: 300px;
  width: 100%;
  border: 2px solid var(--pink);
  border-radius: 0.5rem;
}

.project-page-header-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: var(--lora);
  font-weight: var(--fw-regular);
  width: 60%;
}

.project-experience {
  color: var(--black);
  margin: 0.2rem !important;
  font-size: clamp(var(--fs-xl), 1rem, var(--fs-3xl));
}

.project-name {
  color: var(--pink);
  margin: 0;
  text-align: center;
  font-size: clamp(var(--fs-2xl), 1.2rem, var(--fs-5xl));
}

.line-break {
  margin: 1.5rem 0 0.2rem 0;
  border: none;
  border-top: 2px solid var(--pink);
  width: 100%;
}

.section-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.8rem;
  color: var(--pink);
  font-family: var(--lora);
  font-weight: var(--fw-light);
  font-size: clamp(var(--fs-md), 4vw, var(--fs-lg));
  margin: 1.1rem 0 1.3rem 0;
}

.heart {
  width: 2.4rem;
  margin-bottom: 0;
}

.section-header h2 {
  margin: 0;
}

.coming-soon {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0;
  font-size: clamp(var(--fs-2xl), 5vw, var(--fs-4xl));
  font-family: var(--chivo);
  color: var(--pink);
  background-image: url("../assets/coming-soon-bg.gif");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.blur-text {
  margin: 0 0 1.5rem 0;
  padding: 0;
  justify-content: center;
  width: 90vw;
  word-break: break-word;
}

/* MEDIA QUERIES */
@media (max-width: 900px) {
  .project-page-container {
    flex-direction: column;
    padding: 1rem;
    margin: 0 auto;
  }

  .toc {
    display: none; /* hide entire TOC */
  }

  .back-button.mobile {
    display: flex;
    padding: 0;
    margin: 1.5rem auto 0 auto;
    font-size: clamp(var(--fs-sm), 2rem, var(--fs-md));
  }

  .project-page-header {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .project-page-header-text {
    width: 90%;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .project-page-container {
    max-width: 100vw;
  }

  .project-page {
    margin: 0;
  }

  .text-header {
    margin: 0.8rem 0 0.4rem 0;
  }

  .section-header {
    margin: 0.6rem 0 0.9rem 0;
  }

  .coming-soon {
    font-size: 2.4rem;
  }
}
